@import url(https://fonts.googleapis.com/css2?family=Commissioner:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
html {
  /*scroll-behavior: smooth;*/
  font-size: 15px;
  color: #fff;
}

* {
  box-sizing: border-box;
}

body {
  background-color: #181818;
  font-family: "Commissioner", "Helvetica", "Segoe UI", sans-serif;
}

body *:focus {
  outline-style: dashed;
  outline-width: 1px;
  outline-offset: 2px;
  outline-color: #fff;
}

main {
  margin: 0 auto 0 auto;
  width: 62.8%;
}

a,
a:link,
a:visited,
a:hover,
a:active {
  text-decoration: none;
}

p {
  font-family: "Inter";

}

nav {
  position: fixed;
  z-index: 1;
  top: 0;
  display: grid;
  grid-template-columns: auto 0.19fr 0.38fr 0.19fr auto;
  grid-template-rows: auto 0px;
  height: 90px;
  width: 100%;
  background-color: #181818;
  border-bottom: 1px solid #333;
}

.nav-button-clicked {
  border-radius: 10px;
  display: inline-block;
  border: 0;
  padding: 4px 0 6px 0;
  font-family: inherit;
  font-weight: 500;
  text-align: center;
  color: #181818;
  background: linear-gradient(
    90deg,
    rgba(75, 172, 181, 1) 0%,
    rgba(0, 212, 255, 1) 100%
  );
}

.nav-button-unclicked {
  border: 0;
  display: inline-block;
  padding: 4px 0 6px 0;
  font-family: inherit;
  font-weight: 500;
  text-align: center;
  font-size: 1rem;
  background-color: inherit;
  opacity: 0.8;
}

.nav-logo {
  grid-column: 2/3;
  margin: auto;
  font-size: 1rem;
  color: #fff;
  opacity: 0.8;
  font-weight: 900;
  cursor: default;
}

.nav-sections {
  display: grid;
  width: 100%;
  margin: auto 0;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr 1fr;
  grid-row-gap: 2px;
}

.nav-progress-bar {
  height: 2px;
  background: linear-gradient(90deg, #234e52 0%, rgba(0, 212, 255, 1) 100%);
  border-radius: 2px;
  width: 100%;
  grid-column: 1/-1;
  grid-row: 2/3;
  justify-self: start;
  transform-origin: left;
}

.social-link-wrapper {
  margin: auto;
}

.social-link {
  display: inline-block;
  margin: 0 8px;
  font-size: 2rem;
  color: #fff;
  opacity: 0.8;
}

@media only screen and (max-width: 1500px) {
  nav {
    height: 200px;
  }

  .nav-sections {
    display: grid;
    width: 100%;
    margin: auto 0;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(6, 1fr);
    grid-row-gap: 2px;
  }
}
.introduction {
    display: flex;
    color: #fff;
    padding-top: 125px;
    padding-left: 15px;
    cursor: default;
}

.introduction img {
    border-radius: 150px;
}

.introduction section {
    margin: auto 35px auto 35px;
    line-height: 1.5;
}

.introduction header {
    margin-bottom: 5px;
    margin-left: -25px;
    text-transform: uppercase;
    font-size: 2rem;
    font-weight: 800;
    background: linear-gradient(
        90deg,
        rgb(60, 138, 145) 0%,
        rgb(255, 255, 255) 50%,
        rgb(60, 138, 145) 100%
      );
    -webkit-background-clip: text;
            background-clip: text;
    background-size: 500%;
    -webkit-text-fill-color: transparent;
}

.introduction header:hover::after {
 content:' !';

} 

@media only screen and (max-width: 1500px) {
    .introduction {
    padding-top: 235px;
    }
}

@media only screen and (max-width: 1200px) {
    .introduction {
        flex-direction: column;
    }

    .introduction > img {
        margin-bottom: 25px;
    }
}
.entry-card {
    display: flex;
    flex-direction: column;
    border-radius: 25px;
    margin: 25px 15px 0 15px;
}

.entry-card-main {
    border-radius: 25px 25px 0 0;
    background: rgba(75, 172, 181, 1);
    padding: 17px 20px;
    flex-grow: 1;
}

.entry-card-more {
    border-radius: 0 0 25px 25px;
    padding: 17px 20px;
    font-size: 0.95rem;
    background: #E6FFFA;
    color: #234E52;
}

.entry-title {
    font-size: 0.95rem;
    font-weight: 700;
    margin-bottom: 10px;
    text-transform: uppercase;
}

.entry-card-est {
    text-transform: uppercase;
    margin-bottom: 8px;
    font-weight: 700;
}

.entry-card-loc {
    font-size: 0.95rem;
}

.entry-description {
    line-height: 1.3;
    font-weight: 500;
}

.entry-card-kw {
    display: inline-block;
    margin: 8px 3px 0 3px;
    padding: 5px 8px;
    border-radius: 15px;
    background-color: rgba(75, 172, 181, 1);
    color: #fff;
    font-weight: 700;
}

.entry-card-kw-label::before {
    content: '#';
}

#i05 > div {
    display: flex;
    flex-direction: row;
}

#i05 .entry-card-more {
    border-radius: 25px;
    flex-grow: 1;

}

/* compétences majeures */

#i03 > div {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows: 0.7fr 0.3fr 1fr;
}

#i030 {
    grid-column: 1/2;
    grid-row: 1/3;
}

#i031 {
    grid-column: 2/3;
    grid-row: 1/2;
}

#i032 {
    grid-column: 1/2;
    grid-row: 3/4;
}

#i032 {
    grid-column: 2/3;
    grid-row: 2/4;
}


@media only screen and (max-width: 1500px) {
    #i05 > div {
        grid-template-columns: repeat(2,1fr);
    }
}
.cv-section {
    margin-top: 55px;
    margin-left: 25px;
    margin-right: 25px;
    cursor: default;
}

.cv-section::before {
    content: '';
    margin-top: -115px;
    padding-top: 115px;
    display: block;
}

.cv-section::after{
    content: '';
    margin-bottom: 90px;
    padding-bottom: -180px;
    display: block;
}

.cv-section-title {
    padding-top: 5px;
    font-size: 2rem;
    font-weight: 800;
    text-transform: uppercase;
    background: linear-gradient(
        90deg,
        rgb(60, 138, 145) 0%,
        rgb(255, 255, 255) 50%,
        rgb(60, 138, 145) 100%
      );
    -webkit-background-clip: text;
            background-clip: text;
    background-size: 500%;
    -webkit-text-fill-color: transparent; 
}

.cv-section-title:hover::after {
    content: ' >';
}

@media only screen and (max-width: 1500px) {
    .cv-section::before {
        content: '';
        margin-top: -225px;
        padding-top: 225px;
        display: block;
    }
}
footer {
    --footer-height: calc(90px*0.618);
    height: var(--footer-height);
    display: flex;
    width: 100%;
    margin-top: calc(0.618*var(--footer-height));
    border-top: 1px solid rgba(255,255,255,0.1);
}

footer::after {
    content: 'Work In Progress';
    margin: auto;
}
